const documentArr = ["Identity Document", "Work experience", "Education"];
const otherArr = ["Other"];
export const uploadedDocuments = (documents = []) => {
    return documents.map((documentItem, documentIndex) => {
        const { name, document, document_type, id, verified } = documentItem;
        if (document_type === DOCUMENT_TYPE_NAME.OTHER) {
            return {
                id,
                items: [
                    { value: documentIndex + 1 },
                    { value: "Other" },
                    { value: name, isDocument: true, document },
                    { value: STATUS_TYPES.UNVERIFIED, isStatus: true },
                    { isAction: true, value: verified },
                ],
            };
        } else {
            return {
                id,
                items: [
                    { value: documentIndex + 1 },
                    { value: DOCUMENT_TYPE_NAME_ORG[document_type] },
                    { value: name, isDocument: true, document },
                    { value: STATUS_TYPES.UNVERIFIED, isStatus: true },
                    { isAction: true, value: verified },
                ],
            };
        }
    });
};
