export const getTableData = (data = []) => {
    // firstly we need to get the no of documents with document type 4
    const othersArr = [
        {
            id: 4,
            title: SERVICES.OTHER,
            uploadedFile: null,
            docId: null,
        },
    ];
    const normalDataArr = [
        {
            id: 1,
            title: SERVICES.RIGHT_OF_WORK,
            uploadedFile: null,
            docId: null,
        },
        {
            id: 2,
            title: SERVICES.INSURANCE_DOCUMENT,
            uploadedFile: null,
            docId: null,
        },
        {
            id: 3,
            title: SERVICES.EDUCATION,
            uploadedFile: null,
            docId: null,
        },
    ];
    let othersType = 4;
    let index = 0;
    for (let i in data) {
        if (data[i].document_type == DOCUMENT_TYPE_NAME.OTHER) {
            othersArr[index] = {
                id: othersType,
                title: SERVICES.OTHER,
                uploadedFile: data[i]?.document ?? null,
                docId: data[i]?.id ?? null,
            };
            index++;
            othersType++;
        } else {
            normalDataArr[DOCUMENT_TYPE_REV[data[i].document_type]] = {
                id: DOCUMENT_TYPE_REV[data[i].document_type],
                title: DOCUMENT_TYPE_NAME_REV[DOCUMENT_TYPE_REV[data[i].document_type]],
                uploadedFile: data[i]?.document ?? null,
                docId: data[i]?.id ?? null,
                document_type: data[i]?.document_type ?? null,
                valid_till: data[i]?.valid_till ?? null,
            };
        }
    }
    return [...normalDataArr, ...othersArr];
};
export const createUploadPayload = (
    document,
    document_type = "",
    org_doc_type = false,
    uploadId = "",
) => {
    return {
        document_id: uploadId,
        document,
        document_type: org_doc_type
            ? document_type
            : document_type >= 4
            ? 4
            : DOCUMENT_TYPE[document_type],
        name: document.name,
    };
};
export const createRegistrationPayload = (
    data,
    country,
    state,
    phone_number,
    country_code,
    photo,
) => {
    
    const { identity_documents, ...restFormData } = data;
    const formData = { ...restFormData, country, state, phone_number, country_code, photo };
    return { identity_documents, formData };
};
export const createProfileVerification = (data, computerSystems) => {
    console.log(data, computerSystems);
    const cs_data = [];
    computerSystems.map((item, index) => {
        if (item.checked) {
            cs_data.push({
                id: item.id,
            });
        }
    });
    const { registration_number, insurance_validity } = data;
    return {
        reg_number: registration_number,
        valid_till: insurance_validity,
        cs: JSON.stringify(cs_data),
    };
};
