function* uploadDocs() {
    yield all([
        takeLatest(UPLOAD_DOCUMENTS, sendDocuments),
        takeLatest(DELETE_DOCUMENTS, deleteDocuments),
        takeLatest(CV_MANAGE, cvUploadData),
        takeLatest(GET_CV_DATA, getCvData),
        takeLatest(SAVE_JOB_DETAILS, saveJobHistory),
        takeLatest(GET_JOB_DETAILS, getJobHistoryData),
        takeLatest(GET_JOB_DETAILS, getJobHistoryData),
        takeLatest(DELETE_JOB_DETAILS, deleteJobHistoryData),
        takeLatest(SAVE_EDUCATION_DETAILS, saveEducationHistory),
        takeLatest(GET_EDUCATION_DETAILS, getEducationDetails),
        takeLatest(UPDATE_PROFILE, updateProfile),
        takeLatest(GET_PROFILE, getProfile),
        takeLatest(DELETE_EDUCATION_DETAILS, deleteEducationHistoryData),

    ]);
}
export default uploadDocs;
