import "./styles.scss";

import { ROUTES, SNACK_MESSAGES, SNACK_VARIANT, STRINGS, USER_TYPE } from "shared/Constants";
import React, { useEffect, useRef, useState } from "react";
import {
    locumDefaultValues,
    locumRegisterData,
    payloadForRegister,
} from "views/authentication/constants";

import CustomForm from "components/CustomForm";
import CustomModal from "components/CustomModal";
import { register } from "redux/Actions/Auth";
import { useDispatch } from "react-redux";
import { useHistory } from "react-router-dom";
import useNotification from "hooks/Notifications";
import { Images } from "shared/Images";
import CustomButton from "components/CustomButton";
import { STATUS_CODES } from "services/Api/Constants";

const LocumRegisterForm = (props) => {
    const dispatch = useDispatch();
    const history = useHistory();
    const [recaptchaToken, setRecaptchaToken] = useState("");
    const [captchaError, setCaptchaError] = useState(false);
    const [msg, sendNotification] = useNotification();
    const [visible, setVisible] = useState(false);
    const onSubmit = (data) => {
        if (recaptchaToken) {
            setCaptchaError(false);
            const payload = payloadForRegister(data, USER_TYPE.LOCUM);
            dispatch(
                register(
                    payload,
                    (response) => {
                        sendNotification({
                            msg: response.data.message,
                            variant: SNACK_VARIANT.SUCCESS,
                        });
                        history.push(ROUTES.VERIFICATION_CODE, {
                            email: data.email,
                            isSignUp: true,
                        });
                    },
                    (error) => {
                        sendNotification({
                            msg: error.message,
                            variant: SNACK_VARIANT.ERROR,
                        });
                    },
                ),
            );
        } else {
            setCaptchaError(true);
        }
    };
    const recaptchaRes = (data) => {
        setRecaptchaToken(data);
    };
    const onModalClose = () => {
        setVisible(false);
    };
    const onProfileButtonPress = () => {
        history.push(ROUTES.PROFILE);
    };
    useEffect(() => {}, []);
    return (
        <>
            <CustomForm
                key={"locum-register-form"}
                onSubmit={onSubmit}
                defaultValues={locumDefaultValues}
                formData={locumRegisterData}
                privacyPolicy={true}
                jobAlert={true}
                reCaptcha={true}
                onRecaptchaChange={recaptchaRes}
                captchaError={captchaError}
                size={"md"}
            />
            <CustomModal isOpen={visible} handleToggle={onModalClose}>
                <div className="text-center register_success">
                    <em className="mb-3">
                        <img width={30} src={Images.CheckIcon} />
                    </em>
                    <h2 className="h5">{STRINGS.SUCCESSFULLY_REGISTERED}</h2>
                    <p>{STRINGS.COMPLETE_PROFILE}</p>
                    <div className="mt-4">
                        <CustomButton
                            className="btn btn-md btn-secondary"
                            title={STRINGS.GO_TO_PROFILE}
                            onClick={onProfileButtonPress}
                        />
                    </div>
                </div>
            </CustomModal>
        </>
    );
};

export default LocumRegisterForm;
