import React from "react";
import { INPUT_TYPES, STRINGS } from "shared/Constants";
import "./styles.scss";
function CustomCheckBox({ checkBoxData = [], onCheckboxSelect = () => { } }) {
    return (
        <>
            {checkBoxData.length ? (
                checkBoxData.map((checkBoxItem, checkboxIndex) => (

                    <div className="col-md-3">
                        <div key={checkboxIndex} className="checkbox mb-3">
                            <input
                                id={checkBoxItem.id}
                                checked={checkBoxItem.value}
                                type={INPUT_TYPES.CHECKBOX}
                                onChange={onCheckboxSelect}
                            />
                            <label>{checkBoxItem.title}</label>
                        </div>
                    </div>
                ))
            ) : (
                <div className="no_result">{STRINGS.NOT_FOUND}</div>
            )
            }
        </>
    );
}

export default React.memo(CustomCheckBox);
