const {
    STRINGS,
    LABEL,
    REGEX,
    ERROR_MESSAGES,
    INPUT_TYPES,
    USER_TYPE,
} = require("shared/Constants");
export const payloadForRegister = (data, user_type) => {
    if (user_type === USER_TYPE.LOCUM) {
        return {
            email: data["email"],
            first_name: data["first-name"],
            last_name: data["last-name"],
            password: data["password"],
            // username: ,
            // dob: "1998-03-11",
            user_type,
        };
    } else {
        return {
            email: data["email"],
            password: data["password"],
            // username: ,
            // dob: "1998-03-11",
            user_type,
        };
    }
};
