function Profile(props) {
    const history = useHistory();
    const dispatch = useDispatch();
    const [msg, sendNotifications] = useNotification()
    const [data, setData] = useState({})
    const [bankData, setBankData] = useState([])
    const [csData, setCsData] = useState([])
    const [] = useState()
    useEffect(() => {
        dispatch(getAllDetails((response) => {
            console.log(response.data,"data at response");
            setData(response.data)
            setBankData(bankDetails(response.data.invoiceDetails))
            setCsData(csInUse(response.data.csInUse))
            sendNotifications({
                msg:response.data.message,
                variant: SNACK_VARIANT.SUCCESS,
            });
        }, (error) => {
            sendNotifications({
                msg:
                    error.response?.data?.message ??
                    error.response?.data?.detail ??
                    error.message,
                variant: SNACK_VARIANT.ERROR,
            });
        }))
    },[])
    return (
        <main className="main">
            <Header />

            <div className="inner_content view_profile_sec">
                <div className="container">
                    <div className="d-flex justify-content-between">
                        <div className="title_h1 mb-4">
                            <h1 className="h6">View Profile</h1>
                        </div>
                        <button
                            className="btn btn-xs btn-primary"
                            onClick={() => {
                                history.push(ROUTES.MANAGE_PROFILE);
                            }}
                        >
                            <img src={Images.EditIcon} alt="" width={20} />
                        </button>
                    </div>
                    <ProfileDetails data={profileDetails(data)} imageURL={imageURL} rating={3} />
                    <ProfileAttributes
                        title={"Computer Systems Familiar With:"}
                        attributes={csData}
                    />
                    <ProfileBankDetails title={"Bank Details"} data={bankData} />
                    <ProfileDescription
                        title={"Descriptions"}
                        description={data?.personal_details?.personal_description ?? ""}
                    />

                    <section className="accordin_sec">
                        <Accordion title={"Education"}>
                            <BasicTable theadData={tableHeader} tbodyData={educationHistoryDetails(data.edu_history)} />
                        </Accordion>
                        <Accordion title={"Experience"}>
                            <BasicTable theadData={tableHeader} tbodyData={jobHistoryDetails(data.job_history)} />
                        </Accordion>
                    </section>

                    <div className="card">
                        <div className="card-body">
                            <h2 className="h6 text-captialize">Uploaded Documents</h2>

                            <FunctionalTable
                                theadData={tableHeaderFunc}
                                tbodyData={uploadedDocuments(data.documents)}
                                handleDocumentClick={handleDocumentClick}
                                handleActionClick={handleActionClick}
                                type={USER_TYPES.USER}
                            />
                        </div>
                    </div>
                </div>
            </div>

            <Footer />
        </main>
    );
}

