import CustomForm from "components/CustomForm";
import useNotification from "hooks/Notifications";
import React, { useEffect, useState } from "react";
import { useDispatch } from "react-redux";
import { SNACK_VARIANT } from "shared/Constants";
import {
    accountDetailsDefaultData,
    createInvoicePayload,
    INVOICE_DETAILS_DATA,
} from "views/authenticated/constants";
import { getAccountDetails, saveAccountDetails } from "../../../../redux/Actions/Auth";

function Invoice({ accountDetails }) {

    const dispatch = useDispatch();
    const [defaultValues,setDefaultValues] = useState(accountDetailsDefaultData(accountDetails))
    const [msg, sendNotifications] = useNotification();
    const onSubmit = (data) => {
        const payload = createInvoicePayload(data);
        setDefaultValues(data)
        dispatch(
            saveAccountDetails(
                payload,
                (response) => {
                    sendNotifications({
                        msg: response.message,
                        variant: SNACK_VARIANT.SUCCESS,
                    });
                },
                (error) => {
                    sendNotifications({
                        msg:
                            error.response.data.message ??
                            error.response.data.detail ??
                            error.message,

                        variant: SNACK_VARIANT.ERROR,
                    });
                },
            ),
        );
    };
    useEffect(() => {
        setDefaultValues(accountDetailsDefaultData(accountDetails))
    },[accountDetails])
    return (
        <div className="tab_content">
            <CustomForm
                onSubmit={onSubmit}
                reCaptcha={false}
                formData={INVOICE_DETAILS_DATA}
                defaultValues={accountDetailsDefaultData(accountDetails)}
            />
        </div>
    );
}

export default Invoice;
